$(document).ready(function(){
    $(".owl-carousel-slider").owlCarousel({
        loop:true,
        margin:10,
        responsiveClass:true,
        responsive:{
            0:{
                items:1,
            },
            600:{
                items:3,
            },
            1000:{
                items:5,
                loop:false,
            }
        }
    });
    $(".test").owlCarousel({
        loop:true,
        margin:10,
        responsiveClass:true,
        responsive:{
            0:{
                items:1,
            },
            600:{
                items:3,
            },
            1000:{
                items:5,
                loop:false,
            }
        }
    });

    $('.bieanale-open__slider').owlCarousel({
        center: true,
        items: 1,
        loop:true,
        margin:10,
    });
    
    $('.projects-open__slider').owlCarousel({
        center: true,
        items: 1,
        loop:true,
        margin:10,
    });

    $('.projects-slider').owlCarousel({
        center: true,
        items: 1,
        loop:true,
        margin:10,
    });

    $('#nav-icon1').click(function(){
        $(this).toggleClass('open');
        $(".dropdown-mobile-menu").toggleClass('active-menu');
        $("body").toggleClass("no-scroll");
        $(".header-mobile-menu_left").toggleClass("no-visible");
    });

    $(window).on('resize, load', function() {
        if ($(window).width() < 768) {
           $('.footer-logo').insertBefore($('.footer_menu>ul'));
           $('.footer_bottom__links').insertAfter($('.footer_menu>ul'));
        }
        if ($(window).width() < 576) {
            $('.footer_bottom__contacts').insertAfter($('.footer_menu>ul'));
            $('.ffooter_bottom__links').insertBefore($('.footer_bottom__contacts'));
         }
      })

      ymaps.ready(function(){
        // Указывается идентификатор HTML-элемента.
        var moscow_map = new ymaps.Map("first_map", {
            center: [59.931431, 30.304612],
            zoom: 18,
            controls: []
        });
        
        MyIconContentLayout = ymaps.templateLayoutFactory.createClass(
            '<div style="color: #FFFFFF; font-weight: bold;">$[properties.iconContent]</div>'
        ),

        myPlacemarkWithContent = new ymaps.Placemark([59.931431, 30.304612], {}, {
            // Опции.
            // Необходимо указать данный тип макета.
            iconLayout: 'default#imageWithContent',
            // Своё изображение иконки метки.
            iconImageHref: './img/ballon.png',
            // Размеры метки.
            iconImageSize: [62, 78],
            // Смещение левого верхнего угла иконки относительно
            // её "ножки" (точки привязки).
            iconImageOffset: [-24, -24],
            // Смещение слоя с содержимым относительно слоя с картинкой.
            iconContentOffset: [15, 15],
            // Макет содержимого.
            iconContentLayout: MyIconContentLayout
        });

        moscow_map.geoObjects
        .add(myPlacemarkWithContent);
    });

});